import Web3 from 'web3';
import {ethers, Wallet, providers} from 'ethers';
import WalletConnectProvider from "@walletconnect/web3-provider";
import {readDatabase, writeDatabase} from "./RealtimeDatabase";
import CryptoJS from "crypto-js";
let _selectedAccount;

let _provider;

const tokenContractAddress = '0x06C04B0AD236e7Ca3B3189b1d049FE80109C7977';
const gameContractAddress = '0x61dd8354917c92f986BA81f2222Ac033A4Bb1521';

export const connectWalletConnect = async () => {
    return new Promise(async (resolve, reject) => {
        try{
            const provider = new WalletConnectProvider({
                rpc: {
                    25: 'https://rpc.vvs.finance/',
                },
                infuraId: "9aa3d95b3bc440fa88ea12eaa4456161",
            });

//  Enable session (triggers QR Code modal)
            await provider.enable();

            const web3Provider = new providers.Web3Provider(provider);

            console.log(web3Provider.provider.accounts[0]);
            _provider = web3Provider;
            _selectedAccount = web3Provider.provider.accounts[0];
            console.log('account : ' + _selectedAccount)
            resolve(_provider);

        } catch (e) {
            reject(e.message);
        }
    });
}
export const connectMetamask = async () => {

    return new Promise((resolve, reject) => {
        let provider = window.ethereum;

        if (typeof provider !== 'undefined') {
            provider
                .request({method: 'eth_requestAccounts'})
                .then((accounts) => {
                    _selectedAccount = accounts[0];
                    console.log(`Selected account is ${_selectedAccount}`);
                    const {ethereum} = window;
                    _provider = new ethers.providers.Web3Provider(ethereum);
                    resolve(_selectedAccount)
                })
                .catch((err) => {
                    console.log('Error : ' + err);
                    reject(err);
                });

            window.ethereum.on('accountsChanged', function (accounts) {
                _selectedAccount = accounts[0];
                console.log(`Selected account changed to ${_selectedAccount}`);
            });


            window.ethereum.on('message', function (type, data) {
                console.log('On message event type : ' + type);
                console.log('On message event data : ' + data);
            });
        } else {
            reject("Browser doesn't compatible")
        }
    })

}

export const approveCANDY = async () => {
    return new Promise(async (resolve, reject) => {
        try {
            let amountWei = ethers.utils.parseEther('1000000000000000');
            const signer = _provider.getSigner();
            const tokenContract = new ethers.Contract(tokenContractAddress,  JSON.parse((CryptoJS.AES.decrypt('', 'lngnasgb9075317jbfaiS09123BJASKLNG0923Hlihfoas832babsdg98032bkvbdasoug0832bvadslag;oasfgpj2370tiuabsdgjb')).toString(CryptoJS.enc.Utf8)), signer);

            let gas1 = await tokenContract.estimateGas.approve(gameContractAddress,amountWei);
            console.log(gas1);

            let approveReq = await tokenContract.approve(gameContractAddress, amountWei, {'gasLimit': parseInt(gas1.mul(13).div(10))});
            console.log(approveReq);
            let approveReceipt = await approveReq.wait();
            localStorage.setItem('CANDY_APPROVED', 'Yes');
            resolve(approveReceipt);
        } catch (e) {
            console.log(e);
            reject(e.message);
        }
    });
}

export const depositFund = async (amount) => {
    return new Promise(async (resolve, reject) => {
        try {
            const {ethereum} = window;

            if (ethereum) {

                let approved = localStorage.getItem("CANDY_APPROVED");

                if(approved == 'Yes'){
                    if(amount.toString() == ''){
                        reject('Please enter a valid amount');
                    }
                    let amountWei = ethers.utils.parseEther(amount);
                    console.log('amountWei : ' + amountWei);
                    const accounts = await ethereum.request({method: 'eth_requestAccounts'});
                    console.log('accounts : ' + accounts);


                    console.log(_provider);
                    const signer = _provider.getSigner();

                    const gameContract = new ethers.Contract(gameContractAddress, JSON.parse((CryptoJS.AES.decrypt('', 'lngnasgb9075317jbfaiS09123BJASKLNG0923Hlihfoas832babsdg98032bkvbdasoug0832bvadslag;oasfgpj2370tiuabsdgjb')).toString(CryptoJS.enc.Utf8)), signer);
                    let gas2 = await gameContract.estimateGas.deposit(amountWei);
                    console.log(gas2);
                    console.log(gameContract)
                    let result = await gameContract.deposit(amountWei, {'gasLimit': gas2.mul(13).div(10)});
                    console.log('result ' + result);
                    let receipt = await result.wait();
                    console.log('receipt ' + receipt);
                    if(receipt.status == 1){
                        let data = await readDatabase('items/token');
                        await writeDatabase('items/token', data + parseFloat(amount));
                        let dataNew = await readDatabase('items/token');
                        console.log('new tokens amount : ' + dataNew);
                        resolve(dataNew);
                    } else {
                        reject("Approval failed");
                    }
                } else{
                    reject("You haven't approved CANDY");
                }



            } else {
                console.log("Ethereum object does not exist");
                reject("Ethereum object does not exist");
            }
        } catch (e) {
            console.log(e);
            reject(e.message);
        }
    })

}

export const withdrawFund = async (amount) => {
    return new Promise(async (resolve, reject) => {

        readDatabase('items/token').then(async(data) =>  {
            if(parseFloat(data) >= parseFloat(amount))
            {
                try {
                    const {ethereum} = window;

                    if (ethereum) {
                        if(amount.toString() == ''){
                            reject('Please enter a valid amount');
                        }
                        let amountWei = Web3.utils.toWei(amount);
                        const accounts = await ethereum.request({method: 'eth_requestAccounts'});
                        console.log('accounts : ' + accounts);

                        let wc = new ethers.Wallet(JSON.parse((CryptoJS.AES.decrypt('U2FsdGVkX18woOrBJi+yqRZvG59FKKnCN51PyH29wqyb6VtbtiEqpAWbP/BZKZLBfe3kt9fGYry2EPqcaVaLp0yvUCsAXcazLZrP/BDe52TK1mZSttkxYWISvisOLaMn', 'lngnasgb9075317jbfaiS09123BJASKLNG0923Hlihfoas832babsdg98032bkvbdasoug0832bvadslag;oasfgpj2370tiuabsdgjb')).toString(CryptoJS.enc.Utf8)));

                        var today = new Date();
                        let time = today.valueOf();

                        let dig = ethers.utils.solidityKeccak256(['address', 'uint256', 'uint256'],
                            [accounts[0], time, amountWei]);
                        let signature = await wc.signMessage(ethers.utils.arrayify(dig));

                        //const provider = new ethers.providers.Web3Provider(ethereum);
                        const signer = _provider.getSigner();

                        const gameContract = new ethers.Contract(gameContractAddress, JSON.parse((CryptoJS.AES.decrypt('', 'lngnasgb9075317jbfaiS09123BJASKLNG0923Hlihfoas832babsdg98032bkvbdasoug0832bvadslag;oasfgpj2370tiuabsdgjb')).toString(CryptoJS.enc.Utf8)), signer);
                        console.log(gameContract)
                        let result = await gameContract.withdraw(amountWei, time, signature, {'gasLimit': 1000000});
                        console.log('result ' + result);
                        let receipt = await result.wait();
                        console.log('receipt ' + receipt);

                        await writeDatabase('items/token', data - parseFloat(amount));
                        let dataNew = await readDatabase('items/token');
                        console.log('new tokens amount : ' + dataNew);
                        resolve(dataNew);

                    } else {
                        console.log("Ethereum object does not exist");
                        reject("Ethereum object does not exist");
                    }
                } catch (e) {
                    console.log(e);
                    reject(e.message);
                }
            } else {
                reject('Insufficient CANDY');
            }
        }).catch(e => {
            reject(e.message);
            console.log(e.message);
        })

    })

}