const ImgUpload = ({
                       onChange,
                       src
                   }) =>
    <label htmlFor="photo-upload" className="custom-file-upload fas">
        <div className="img-wrap img-upload">
            <img for="photo-upload" src={src}/>
        </div>
        <input id="photo-upload" type="file" onChange={onChange}/>
    </label>


const Name = ({
                  onChange,
                  value
              }) =>
    <div className="field">
        <label htmlFor="name">
            name:
        </label>
        <input
            id="name"
            type="text"
            onChange={onChange}
            maxLength="25"
            value={value}
            placeholder="Alexa"
            required/>
    </div>


const Profile = ({
                     onSubmit,
                     src,
                     name,
                     status,
                 }) =>
    <div className="card">
        <form onSubmit={onSubmit}>
            <h1>Profile</h1>
            <label className="custom-file-upload fas">
                <div className="img-wrap">
                    <img for="photo-upload" src={src}/>
                </div>
            </label>
            <div className="name">{name}</div>
            <div className="status">{status}</div>
            <button type="submit" className="submitProfile">Edit Profile</button>
        </form>
    </div>


const Edit = ({
                  onSubmit,
                  children,
              }) =>
    <div className="card">
        <form onSubmit={onSubmit}>
            <h1>Profile Card</h1>
            {children}
            <button type="submit" className="save submitProfile">Save</button>
        </form>
    </div>

class CardProfile extends React.Component {
    state = {
        file: '',
        imagePreviewUrl: 'https://github.com/OlgaKoplik/CodePen/blob/master/profile.jpg?raw=true',
        name: '',
        status: '',
        active: 'edit'
    }

    photoUpload = async e => {
        e.preventDefault();
        const reader = new FileReader();
        const file = e.target.files[0];
        uploadAvatar(file);
        reader.onloadend = () => {
            this.setState({
                file: file,
                imagePreviewUrl: reader.result
            });
        }
        reader.readAsDataURL(file);
    }
    editName = async e => {
        const name = e.target.value;
        await writeDatabase('settings/userName', name);
        this.setState({
            name,
        });
    }

    handleSubmit = e => {
        e.preventDefault();
        let activeP = this.state.active === 'edit' ? 'profile' : 'edit';
        this.props.handleSaveProfileClick();
        this.setState({
            active: activeP,
        })
    }

    componentDidMount(){
        readDatabaseGeneric('settings/avatarURL').then((data)=>{
            this.setState({
                imagePreviewUrl: data
            })
        });

        readDatabaseGeneric('settings/userName').then((data)=>{
            this.setState({
                name: data
            })
        });
    }

    render() {
        const {
            imagePreviewUrl,
            name,
            status,
            active
        } = this.state;

        return (
            <div>
                {(active === 'edit') ? (
                    <Edit onSubmit={this.handleSubmit}>
                        <ImgUpload onChange={this.photoUpload} src={imagePreviewUrl}/>
                        <Name onChange={this.editName} value={name}/>
                    </Edit>
                ) : (
                    <Profile
                        onSubmit={this.handleSubmit}
                        src={imagePreviewUrl}
                        name={name}
                        status={status}/>)}

            </div>
        )
    }
}

