class ProfileInputField extends React.Component {
    constructor() {
        super();
        this.state = {
            type: "text"
        };
    }

    handleInput(e) {
        //const value = e.target.value.replace(/\D/, '');

        this.props.onChange(e.target.value);
    }

    render() {
        return (
            <div>
                <label className='inputFieldLabel'>{this.props.label}</label>
                <input
                    type={this.props.type ? this.props.type : this.state.type}
                    name={this.props.name}
                    onChange={e => this.handleInput(e)}
                    value={this.props.value}
                    className="input"
                />
            </div>
        );
    }
}

class ProfileBox extends React.Component {
    constructor() {
        super();
        this.state = {
            count: 0
        };
    }

    render() {
        return <div className="profileBox">{this.props.children}</div>;
    }
}

export default class UserProfile extends React.Component {

    constructor() {
        super();
        this.state = {
            isFieldsEmpty: true,
            error: {
                amount: false,
                message: "",
                done: false
            },
            amountValue: "",
            user: getAuth().currentUser,
            candy: 0,
            inProgress: false,
            profileEdit: false,
        };
        this.handleInput = this.handleInput.bind(this);
        this.handleDepositClick = this.handleDepositClick.bind(this);
        this.handleWithdrawClick = this.handleWithdrawClick.bind(this);
        this.handleApproveClick = this.handleApproveClick.bind(this);
        this.handleProfileClick = this.handleProfileClick.bind(this);
        this.handleProfileSave = this.handleProfileSave.bind(this);
        readDatabase('items/token').then(data => {
            this.setState({
                candy: data
            })
        }).catch(err => {
            console.log(err.message);
        })
    }

    handleInput(value) {
        console.log(value);
        this.setState(
            {
                amountValue: value
            }
        );
    }

    isFieldsEmpty() {
        if (!this.state.emailValue || !this.state.passValue) {
            /*this.setState({
              formHasError: true,
              errorMsg: 'Please fill all details'
            });*/
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: this.state.amountValue === "",
                    message: "Please enter a valid amount"
                }
            }));
            return true;
        } else {
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: false
                }
            }));
            return false;
        }
    }

    handleDepositClick() {
        this.setState(prevState => ({
            error: {
                ...prevState.error,
                amount: false,
                message: "",
                done: false
            },
            inProgress: true
        }));
        console.log('deposit starting...');
        depositFund(this.state.amountValue).then((r) => {
            console.log(r);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: false,
                    message: "CANDY withdrawn successfully!",
                    done: true
                }
            }));
            this.setState({
                candy: r
            });
        }).catch(e => {
            console.log(e);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: true,
                    message: e
                }
            }));
        }).finally(() => {
            this.setState({

                inProgress: false
            });
        });
    }

    handleApproveClick() {
        this.setState(prevState => ({
            error: {
                ...prevState.error,
                amount: false,
                message: "",
                done: false
            },
            inProgress: true
        }));
        console.log('withdraw starting...');
        approveCANDY().then(r => {
            console.log(r);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: false,
                    message: "CANDY approved",
                    done: true
                }
            }));
        }).catch(e => {
            console.log(e);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: true,
                    message: e
                }
            }));
        }).finally(() => {
            this.setState({

                inProgress: false
            });
        });
    }

    handleProfileClick() {
        console.log('handleProfileClick');
        this.setState(prevState => ({
            profileEdit: true
        }));
    }

    handleProfileSave() {
        console.log('handleProfileClick');
        this.setState(prevState => ({
            profileEdit: false
        }));
    }

    handleWithdrawClick() {
        this.setState(prevState => ({
            error: {
                ...prevState.error,
                amount: false,
                message: "CANDY withdrawn successfully!",
                done: true
            },
            inProgress: true
        }));
        console.log('withdraw starting...');
        withdrawFund(this.state.amountValue).then((r) => {
            console.log(r);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: false,
                    message: "",
                    done: true
                }
            }));
            this.setState({
                candy: r
            });
        }).catch(e => {
            console.log(e);
            this.setState(prevState => ({
                error: {
                    ...prevState.error,
                    amount: true,
                    message: e
                }
            }));
        }).finally(() => {
            this.setState({

                inProgress: false
            });
        });
    }

    render() {
        const {user, inProgress, candy, profileEdit} = this.state;

        if (profileEdit) {
            return (
                <div className="loginContainer">
                    <CardProfile handleSaveProfileClick={this.handleProfileSave}/>
                </div>
            );
        } else {
            return (
                <div className="loginContainer">
                    <IconButton onClick={this.handleProfileClick} class="fab fa-instagram">
                        <Avatar
                            style={{
                                width: "60px",
                                height: "60px",
                            }}
                        />
                    </IconButton>
                    <ProfileBox>
                        {inProgress && <CircularProgress style={{position: 'absolute', left: '50%', top: '50%'}}/>}
                        <ErrorMessage
                            msg={this.state.error.message}
                            show={this.state.error.amount}
                            done={this.state.error.done}
                        />


                        <h4 className='loginOptionText2'>Candy City User ID : {user.uid}</h4>
                        <h4 className='loginOptionText2'>You have : {candy} CANDY in your profile</h4>
                        <br/>
                        <ProfileInputField
                            onChange={this.handleInput}
                            label='Enter amount to deposit or withdraw'
                            name='amount'
                            value={this.state.amountValue}
                        />
                        <ContractCallButtons handleDepositClick={this.handleDepositClick}
                                             handleWithdrawClick={this.handleWithdrawClick}
                                             handleApproveClick={this.handleApproveClick}/>
                    </ProfileBox>
                </div>
            );
        }
    }
}